body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.pagination {
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: center;
  float: bottom;
  background-color: transparent;
  list-style: none;
  width: 30%;
  margin: auto -2% 0 auto;
  padding: 1px 5px;
  color: white;
}

.page-nav {
  cursor: pointer;
  width: min(14%, 22px);
  border-radius: 8%;
  font-size: 1.15rem;
  font-weight: bold;
  background-color: rgb(9, 30, 43);
  padding: 1px 2px;
  margin: 1px 2px;
}

.active {
  background-color: green !important;
  color: white;
}

.disabled {
  cursor: not-allowed !important;
}

.page-button {
  font-size: 1.3rem;
  width: min(24%, 22px);
  border-radius: 20%;
  padding: 1px 2px;
  margin: 1px 2px;
  cursor: pointer;
  background-color: grey;
}
